<template>
    <div class="forget">
        <div class="forget-title">
            <p class="sub-title">歡迎登入</p>
            <p class="title">WELLCOME</p>
        </div>
        <div class="forget-content">
            <p class="title">忘記密碼</p>
            <p class="sub-title">FORGOT PASSWORD</p>
            <el-steps class="steps" :active="active" align-center finish-status="success">
                <el-step title="輸入驗證" icon="el-icon-key"></el-step>
                <el-step title="重設密碼" icon="el-icon-refresh"></el-step>
                <el-step title="設定成功" icon="el-icon-check"></el-step>
            </el-steps>
            <div class="forget-info">
                <div v-if=" active === 0">
                    <p class="forget-info-txt">請輸入註冊時的Email，<br>我們將寄出重設密碼的Email給您。</p>
                    <input class="forget-input" type="text" placeholder="Email" v-model="mail">
                    <div class="forget-btn " @click="next()">送出</div>
                </div>
                <div v-if=" active === 1 " >
                    <p class="forget-info-txt">請重新設定新密碼</p>
                    <input class="forget-input" type="text" placeholder="新密碼" v-model="pws"><br>
                    <input class="forget-input" type="text" placeholder="確認新密碼" v-model="pws">
                    <div class="forget-btn " @click="next()">送出</div>
                </div>
                <div v-if=" active === 2 " class="forget-info-3">
                    <i class="el-icon-check icon" />
                    <p class="check-txt">新密碼設定成功</p>
                    <p class="forget-info-txt">恭喜您的新密碼設定成功！即可重新登入網站</p>
                    <div class="router">
                        <router-link to="/adminLogin">查看管理頁面</router-link> /
                        <router-link to="/">回到首頁</router-link> 
                    </div>

                </div>

            </div>
        </div>


    </div>
</template>

<script>
export default {
    name:'forget',
    data() {
        return{
            active:0,
            mail: '',
            phone: '',
            phoneSms: '',
            pws: '',
        }
    },
    methods: {
        next() {
        if (this.active++ > 3) this.active = 0;
        },
    }

}
</script>

<style lang="scss">
.forget {
    padding: 105px 0;
    .forget-title {
        background: url('~@/assets/img/login/login-banner.png') no-repeat center;
        background-size: cover;
    }  
    .forget-content {
        text-align: center;
        margin: 100px 0;
        .title {
            font-size: 40px;
            color: #333333;
            font-weight: bold;
            margin-bottom: 10px;
        }
        .sub-title {
            font-size: 25px;
            color: #ef6d38;
            margin-bottom: 100px;
        }
        .steps {
            max-width: 650px;
            margin: auto;
            margin-bottom: 50px;
        }
        .forget-info {
            background: #f4f4f4;
            padding: 50px 0;
            .forget-btn {
                max-width: 400px;
                width: 85%;
                line-height: 70px;
                color: #fff;
                border-radius: 20px;
                background: #ef6d38;
                margin: 20px auto;
                cursor: pointer;
                transition: all .3s;
                &:hover {
                    filter: brightness(130%)
                }
            }
            .phone {
                background: #555555;
            }
            .forget-info-txt{
                margin-bottom: 50px;
                line-height: 25px;
                color: #555555;
            }
            .forget-input {
                max-width: 400px;
                width: 85%;
                line-height: 60px;
                border-radius: 20px;
                border: 1px #555 solid;
                padding: 0px 20px;
                box-sizing: border-box;
                margin-bottom: 20px;
            
            }
            .forget-input-phone {
                position: relative;
                max-width: 400px;
                width: 85%;
                margin: auto;
                .forget-input { 
                    width: 100%;
                }
                span {
                    right: 3px;
                    top: 3px;
                    position: absolute;
                    background: #ef6d38;
                    color: #fff;
                    border-radius: 0px 18px 18px 0px;
                    line-height: 56px;
                    padding: 0 10px; 
                    cursor: pointer;
                    &::after {
                        content: " ";
                        position: absolute;
                        left: -30px;
                        border-style: solid;
                        border-width: 0 0 56px 30px;
                        border-color: transparent transparent #ef6d38 transparent;
                    }  
                }
            }
            .forget-info-3 {
                padding: 50px 0;
                .icon {
                    padding: 10px;
                    background: #2bb673;
                    border-radius: 50px;
                    font-size: 60px;
                    color: #fff;
                }
                .check-txt {
                    color: #2bb673;
                    font-size: 24px;
                    margin: 15px 0 30px 0;
                }
                .forget-info-txt {
                    margin-bottom: 20px;
                }
                .router {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    a {
                        color: #ef6d38;
                        margin: 0 5px;
                    }
                }
            }
        }
    }
}

</style>


